* {
    font-family: 'Oxygen', sans-serif;
}

body {
    height: 100%;
    width: 100%;
    padding: 0;
    margin: 0;
}

.app {
    position: fixed;
    width: 100%;
    height: 100%;
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex;
}

.content {
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex;
    -webkit-box-orient: vertical;
    -webkit-box-direction: normal;
        -ms-flex-direction: column;
            flex-direction: column;
    -webkit-box-flex: 1;
        -ms-flex: 1 1 auto;
            flex: 1 1 auto;
    background-color: #f8f9fa;
    min-width: 0;
}

.demo {
    -webkit-box-flex: 1;
        -ms-flex: 1;
            flex: 1;
    overflow: auto;
    min-width: 0;
}

.wide-page {
    margin: 50px auto;
    padding: 0 50px;
}

.simple-page {
    max-width: 500px;
    margin: 50px auto;
}
.header {
    height: 60px;
    background-color: #41b883;
    -webkit-box-shadow: 2px 0px 4px #ccc;
            box-shadow: 2px 0px 4px #ccc;
    z-index: 0;
    color: white;
    font-size: 20px;
    padding: 0 50px;
    vertical-align: middle;
    line-height: 60px;
}

.header.open {
    padding: 0 20px;
}

.source-code {
    float: right;
    height: 60px;
    line-height: 60px;
    vertical-align: middle;
    cursor: pointer;
}

.source-code img {
    width: 30px;
    height: 30px;
    vertical-align: middle;
}

.source-code span {
    font-size: 14px;
    color: #eee;
    margin-left: 10px;
    vertical-align: middle;
}
.nav-button {
    display: none;
    position: fixed;
    left: 10px;
    top: 20px;
    z-index: 3;
    width: 30px;
    height: 30px;
    -webkit-transform: rotate(0deg);
    transform: rotate(0deg);
    -webkit-transition: .3s ease-in-out;
    transition: .3s ease-in-out;
    cursor: pointer;
}

.nav-button.open {
    left: 310px;
}

.nav-button.open span {
    background: #ccc;
}

.nav-button span {
    display: block;
    position: absolute;
    height: 4px;
    width: 100%;
    background: #fff;
    border-radius: 4px;
    opacity: 1;
    left: 0;
    -webkit-transform: rotate(0deg);
    transform: rotate(0deg);
    -webkit-transition: .25s ease-in-out;
    transition: .25s ease-in-out;
}

/* Icon 3 */

.nav-button span:nth-child(1) {
    top: 0px;
}

.nav-button span:nth-child(2), .nav-button span:nth-child(3) {
    top: 8px;
}

.nav-button span:nth-child(4) {
    top: 16px;
}

.nav-button.open span:nth-child(1) {
    top: 18px;
    width: 0%;
    left: 50%;
}

.nav-button.open span:nth-child(2) {
    -webkit-transform: rotate(45deg);
    transform: rotate(45deg);
}

.nav-button.open span:nth-child(3) {
    -webkit-transform: rotate(-45deg);
    transform: rotate(-45deg);
}

.nav-button.open span:nth-child(4) {
    top: 8px;
    width: 0%;
    left: 50%;
}

.navigator {
    width: 350px;
    -ms-flex-negative: 0;
        flex-shrink: 0;
    -webkit-box-shadow: 0px 2px 5px #ccc;
            box-shadow: 0px 2px 5px #ccc;
    background-color: white;
    z-index: 1;
    -webkit-transition: width .3s ease-in-out;
    transition: width .3s ease-in-out;
    overflow: hidden;
}

@media (max-width: 700px) {
    .navigator {
        position: fixed;
        height: 100%;
        left: 0;
        top: 0;
        bottom: 0;
    }

    .nav-button {
        display: block;
    }
}

.nav-panel {
    -ms-flex-negative: 0;
        flex-shrink: 0;
    width: 300px;
}

.nav.closed {
    width: 0;
}

.nav-content {
    padding-top: 0px;
    width: 350px;
    height: 100%;
    overflow-y: auto;
}

.nav-header h3 {
    padding-left: 10px;
}

.divider {
    height: 1px;
    border-bottom: 1px solid #ddd;
    margin-top: -2px;
}

.menu-section h4 {
    color: #444;
    padding-left: 10px;
}

.menu-section ul {
    color: #666;
    list-style: none;
    padding: 0;
    margin: 0;
}

.menu-section li {
    font-size: 14px;
    margin: 0;
    padding: .5em .5em .5em 30px;
    cursor: pointer;
    margin-bottom: 5px;
}

.menu-section li:hover {
    background-color: ghostwhite;
}

.router-link-active,
.menu-section li.selected {
    background-color: ghostwhite;
}


.draggable-item {
    height: 50px;
    line-height: 50px;
    text-align: center;
    width: 100%;
    display: block;
    background-color: #fff;
    outline: 0;
    border: 1px solid rgba(0, 0, 0, .125);
    margin-bottom: 2px;
    margin-top: 2px;
    cursor: default;
    -webkit-user-select: none;
       -moz-user-select: none;
        -ms-user-select: none;
            user-select: none;
}

.draggable-item-horizontal {
    height: 300px;
    padding: 10px;
    line-height: 100px;
    text-align: center;
    /* width : 200px; */
    display: block;
    background-color: #fff;
    outline: 0;
    border: 1px solid rgba(0, 0, 0, .125);
    margin-right: 4px;
    cursor: default;
}

.dragging {
    background-color: yellow;
}

