    padding: 50px;
    /* background-color: #fff; */
}

.card-container {
    width: 320px;
    padding: 10px;
    margin: 5px;
    margin-right: 45px;
    background-color: #f3f3f3;
    -webkit-box-shadow: 0 1px 1px rgba(0, 0, 0, 0.12), 0 1px 1px rgba(0, 0, 0, 0.24);
            box-shadow: 0 1px 1px rgba(0, 0, 0, 0.12), 0 1px 1px rgba(0, 0, 0, 0.24);
}

.card {
    margin: 5px;
    /* border: 1px solid #ccc; */
    background-color: white;
    -webkit-box-shadow: 0 1px 1px rgba(0, 0, 0, 0.12), 0 1px 1px rgba(0, 0, 0, 0.24);
            box-shadow: 0 1px 1px rgba(0, 0, 0, 0.12), 0 1px 1px rgba(0, 0, 0, 0.24);
    padding: 10px;
}

.card-column-header {
    font-size: 18px;
}

.column-drag-handle {
    cursor: move;
    padding: 5px;
}

.card-ghost {
    -webkit-transition: -webkit-transform 0.18s ease;
    transition: -webkit-transform 0.18s ease;
    transition: transform 0.18s ease;
    transition: transform 0.18s ease, -webkit-transform 0.18s ease;
    -webkit-transform: rotateZ(5deg);
            transform: rotateZ(5deg)
}

.card-ghost-drop {
    -webkit-transition: -webkit-transform 0.18s ease-in-out;
    transition: -webkit-transform 0.18s ease-in-out;
    transition: transform 0.18s ease-in-out;
    transition: transform 0.18s ease-in-out, -webkit-transform 0.18s ease-in-out;
    -webkit-transform: rotateZ(0deg);
            transform: rotateZ(0deg)
}

.opacity-ghost {
    -webkit-transition: all .18s ease;
    transition: all .18s ease;
    opacity: 0.8;
    /* transform: rotateZ(5deg); */
    background-color: cornflowerblue;
    -webkit-box-shadow: 3px 3px 10px 3px rgba(0, 0, 0, 0.3);
            box-shadow: 3px 3px 10px 3px rgba(0, 0, 0, 0.3);
}

.opacity-ghost-drop {
    opacity: 1;
    /* transform: rotateZ(0deg); */
    background-color: white;
    -webkit-box-shadow: 3px 3px 10px 3px rgba(0, 0, 0, 0.0);
            box-shadow: 3px 3px 10px 3px rgba(0, 0, 0, 0.0);
}

